          <h2 class="mine-t">“Mine, Minecraft Fest 2023”</h2>
          <p class="mine-p">21 Февраля 2022</p>
          <p class="mine-txt">
            Minecraft недавно добавила отделку брони как часть обновления 1.20. Обновление еще не <br> выпущено, и
            официальной даты выпуска на 2023 год нет, но оно было опубликовано как часть <br> последних снимков.
            <br>
            <br>
            Эти экспериментальные функции дают игрокам доступ к грядущим обновлениям игры, и ясно, что <br> одного
            пользователя Redditor вдохновила новая отделка брони.
            <br>
            <br>
            <br>
            <br>
            Интересны отделки, представленные на Redditor. Первое, что делает игрок в Майнкрафт, — ставит <br> незеритовый
            меч с алмазной отделкой оружия. <br>
            <br>
            <br>
            Получился алмазный меч, заключенный в нетерит, который выглядел намного лучше. Все отделки <br> визуально
            более привлекательны, чем стандартное зачарованное оружие.
            <br>
            <br>
            <br>
            <br>
            Пак является отличным дополнением к игре, которое многие игроки хотели бы вскоре увидеть в <br> ванильной
            версии. <br>
            <br>
            Один комментатор сказал, что считает это отличным и довольно полезным изменением:
            <br>
            <br>
            «Мне нравится идея накладок для инструментов. Это значительно облегчило бы отслеживание <br> моих конкретных
            зачарованных инструментов с первого взгляда, чем наведение курсора на его <br> название и просмотр чар. Я хочу
            и надеюсь, что накладки появятся на Элитре (для игроков, <br> которые у них нет плащей, и можно дополнительно
            настроить их надкрылья с плащами), а также <br> для конских доспехов (представьте себе войны фракций с
            конскими нарядами для <br> соответствующих команд!).
            <br>
            <br>
            <br>
            <br>
